}

.oe-control select {
    width: 150px;
    width:100%;
    outline:none;
    border-radius: 8px;
    height:68px !important;
    padding: 5px 35px 5px 5px;
    font-size: 16px;
    border: 0px solid #ccc;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
