}

.oe-control.middle select {
  height:48px !important;
}

.oe-checkbox, 
.oe-radio {
  width: 24px;
  height: 24px;
  position: relative;
  /*box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5);*/ 
}

.oe-checkbox .oe-border {
  width: 24px;
  height: 24px;
  background: #F2F2F2;
  border-radius: 12px;
  position: absolute;
  /*box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5);*/ 
}

.oe-radio .oe-border {
  width: 24px;
  height: 24px;
  background: #F2F2F2;
  border-radius: 8px;
  position: absolute;
  /*box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5);*/ 
}

.oe-radio label {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 4px;
  transition: all .5s ease;
  cursor: pointer;
  position: absolute;
  top: 6px;
  left: 6px;
  z-index: 1;
  background:#F2F2F2;
  /*box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.5); */
}

.oe-checkbox label {
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 12px;
  transition: all .5s ease;
  cursor: pointer;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1;
  background:#F2F2F2;
  border: 1px solid #666;
}

.oe-checkbox label span, 
.oe-radio label span {
  position:absolute;
  left: 28px;
  top:0px;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: normal;
  letter-spacing: 0.05em;  
  // min-width: 500px;
  white-space: pre;
}

.oe-checkbox label span {
  top:1px;
}

.oe-control.tal label span {
  text-align: left;
}

.oe-checkbox label span.blue {
  color:#4EAEC7;
}

.oe-checkbox input[type=checkbox]:checked + label,
.oe-radio input[type=radio]:checked ~ div.oe-border {
  background: #AAB338; 
}

.oe-radio input[type=radio]:checked + label {
  background: #333333; 
}

.oe-checkbox input[type=checkbox]:checked + label:after {
  opacity: 1;
  content: '';
  position: absolute;
  width: 10px;
  height: 7px;
  background: transparent;
  top: 6px;
  left: 6px;
  border: 3px solid #333;
  border-top: none;
  border-right: none;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}



.input-group {
  position: relative;
  margin: 0px 0 0px;
  background: #F2F2F2;
  border-radius: 8px 8px 0px 0px;
}

input {
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  border: none;
  margin-top:30px;
  border-bottom: 1px solid #757575;
}

input[type="text"]:focus {
  outline: none;
}

.input-group label.oe-label {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: normal;
  color: #333;
  opacity: 0.5;

  position: absolute;
  pointer-events: none;
  left: 13px;
  top: 35px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

#app.edge .input-group label.oe-label {
  top: 5px !important;
  font-size: 10px !important;
  color:#333;
}

.input-group input[type="text"]:focus ~ label.oe-label {
  top: 5px;
  line-height: 15px;
  font-size: 10px;
  color: #333;
  opacity: 1;
}

.input-group input:not(:placeholder-shown) ~ .oe-label {
  top: 5px;
  line-height: 15px;
  font-size: 10px;
  color: #333;
  opacity: 1;  
}

.input-group input:not(:-ms-input-placeholder) ~ .oe-label {
  top: 5px;
  line-height: 15px;
  font-size: 10px;
  color: #333;
  opacity: 1; 
}

.input-group .bar {
  position: relative;
  display:block;
  width:100%;
  margin: 0px 0px;
}

.bar:before,
.bar:after {
  content: '';
  height: 1px;
  width: 50%;
  bottom: 1px;
  position: absolute;
  background: #333;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.bar:before {
  left: 50%;
}

.bar:after {
  right: 50%;
}

input:focus ~ .bar:before,
input:focus ~ .bar:after {
  width: 50%;
  background: #AAB338;
}

.highlight {
  position: absolute;
  height: 60%;
  width: 100px;
  top: 25%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
}

input:focus ~ .highlight {
  -webkit-animation: inputHighlighter 0.3s ease;
  -moz-animation: inputHighlighter 0.3s ease;
  animation: inputHighlighter 0.3s ease;
}

.oe-icon img:hover {
  filter: invert(13%) sepia(96%) saturate(7400%) hue-rotate(359deg) brightness(101%) contrast(110%);
}

