}



//toggle
.oe-control .oe-toggle {
  width:200px;
}

.oe-control .oe-toggle
		input[type=checkbox]{
			height: 0;
			width: 0;
			visibility: hidden;
		}

    .oe-control .oe-toggle
		label {
			cursor: pointer;
			text-indent: -9999px;
			width: 39px;
			height: 24px;
			display: block;
			border-radius: 100px;
			position: relative;
      border: 1px solid gray;
      float:right;
      top:-3px;
		}
		
		.oe-control .oe-toggle
		label:after {
			content: '';
			position: absolute;
			top: 2px;
			left: 2px;
			width: 18px;
			height: 18px;
			background: $t1_error;
			border-radius: 90px;
			transition: 0.3s;
		}

		.oe-control .oe-toggle
		input:checked + label:after {
			left: calc(100% - 2px);
			transform: translateX(-100%);
			background: $t1_success;
		}

		.oe-control .oe-toggle
		label:active:after {
			width: 130px;
		}
		
		.oe-control .oe-toggle {
			position: relative;
		}

		.oe-control .oe-toggle span {
			left: 0px;
      top:3px;
      float:left;
      font-size:10px;
    }

