@import '_main.scss';

.__react_component_tooltip {
    max-width:400px;
}

.block-full {
    padding:0px !important;
}

.block-inner {
    position: relative;
    margin-bottom: 5px;
}

.block-innerMiddle {
    position: relative;
    margin-bottom: 5px;
    margin-left: 5%;
    margin-right: 5%;
}

.block-orderProducts {
    position: relative;
    margin-bottom: 5px;
    margin-left: 15%;
    margin-right: 15%;
}


.block.block-inner.middle {
    margin-left: 32px;
    
}

.block-act-contract {
    margin-top: 55px;
    height:140px;
}

.block-contract{
    position: relative;
    margin-bottom:50px;
}

.block-act-contract .thumb-brand-blister{
    position:absolute;
    top:-40px;
}

.block-act-contract .block-inner {
    margin-left: 160px;
}

.block-contract .action {
    position:absolute;
    right:24px;
    top: 24px;
    z-index: 995;
}

.block-contract .action-top {
    position:absolute;
    right:24px;
    top: -32px;
    z-index: 995;
}

.apistatus {
    position: relative;
    min-height: 100px;
    width:100%;
}

.apistatus .info {
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 40px;
    margin-top: $dim;
    color: #828282;
} 

.apistatus .act {
    position: absolute;
    right:24px;
    top:-2px;
}

.apistatus .details {
    width:80%;
}

.spacer-line {
    background: #E0E0E0;
    width:100%;
    height:1px;
}

.spacer {
    height:8px;
    width:100%;
}

.spacer-medium {
    width:100%;
    height:24px;
}

.block-act-contract.block-act-preview {
    height: auto;
}

.block-act-preview .container {
    margin-left: 160px;
    width:auto;
}

.block-act-preview .container.full {
    margin-left: 0px !important;
    width:auto;
}

.block.clue {
    border: 1px solid #AAB338 !important;
    padding: 16px !important;
}

.block.extra {
    border: 1px solid #8F359B !important;
    padding: 16px !important;
}

.block.error {
    border: 0px;
    background: $t1_error_background;
    color: $t1_error_foreground;
}

.block.clue .title {
    margin-bottom:5px;
}

.block.clue .subtitle {
    color: #AAB338 !important;
    margin-bottom: 20px;
    font-style: normal;
    font-weight: 900;
    font-size: 12px;
    line-height: normal; 
    letter-spacing: 0.05em;
    margin-bottom:5px;
}

.block.extra .etitle {
    font-family: Muli;
    font-style: normal;
    font-weight: 600;
    font-size: 20px !important;
    line-height: 25px;
    display: flex;
    align-items: center;
    color: #333333;
}

.block.extra .esubtitle {
    font-family: Muli;
    font-style: normal;
    font-weight: bold;
    font-size: 12px !important;
    line-height: 15px;
    display: flex;
    align-items: center;
    letter-spacing: 0.05em;
    color: #8F359B !important;
}

.block.extra .einfo {
font-family: Muli;
font-style: normal;
font-weight: 600;
font-size: 10px;
line-height: 15px;
color: #828282;
margin-top:16px;
}

.block.hot {
    background: linear-gradient(0deg, rgba(170, 179, 56, 0.25), rgba(170, 179, 56, 0.25)), #FFFFFF;
}


.timer {
    position: relative;
    width: 30px;
    height: 30px;
}

.timer .bg {
    background: url('../assets/images/loaderr.gif') no-repeat;
    position: absolute;
    width: 30px;
    height: 30px;
    background-size: contain;    
}

.timer .inner {
    position: absolute;
    left: 20px;
    top: 0px;
    font-size:10px;
}


  .circle-chart__circle {
    transform: rotate(-45deg);
    transform-origin: center;
  }
  
  .loadersvg {
      position:relative;
      width:94px;
      height:94px;
  }

  .loadersvg .background {
      position:absolute;
      top:0;
      left:0;
  }

  .loadersvg .info {
      position:absolute;
      font-family: Muli;
      font-style: normal;
      font-weight: bold;
      font-size: 32px;
      line-height: 40px;
      width:100%;
      height:100%;
      text-align:center;
      margin:auto 25px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #DF9A36;
  }

  .loadersvg .info.single {
      margin-left: 15px;
  }  


  .__react_component_tooltip {
    -webkit-box-shadow: 0px 0px 8px 0px rgba(79,79,79,1);
    -moz-box-shadow: 0px 0px 8px 0px rgba(79,79,79,1);
    box-shadow: 0px 0px 8px 0px rgba(79,79,79,1);
    padding:16px !important;
  }

  .oe-tooltip .product {
    width: 296px;
    /*height: 216px;*/
  }

  .oe-tooltip .product .title {
      margin-bottom: 8px;
  }

  .oe-tooltip .product .spacer-line {
      margin:8px 0px;
  }


  @-webkit-keyframes autofill {
    to {
        background: transparent;
    }
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    -webkit-animation-name: autofill;
    -webkit-animation-fill-mode: both;
}

.group-member {
    margin-bottom: $dimx2;
}

.group-text {
    padding: $dim;
}

.group-text-big {
    padding: 5px;
}

.group-text.cardowner {
    color: $t1_brand2_text !important;
}

.group-text.cardmember {
    color: $t1_table_sum !important;
}

.group-member .container {
    padding:0px 4px;
}

.block-download {
    position: relative;
    height: $dimx11;
    width: 400px;
    border: 1px solid #4EAEC7;
    box-sizing: border-box;
    border-radius: $dimx2;
    padding: $dimx3;
    float:left;
    margin-left: $dim;
    margin-bottom: $dim;
}

.block-download .action {
    position:absolute;
    right: $dimx3;
    top: $dimx3;
}

iframe.scanner {
    width: 100%;
    height:1550px;
    border: 0px;
}   

.react-datepicker__day--selected {
    background: $t1_input_base !important;
    color: black !important;
}

.defcon {
    position: absolute;
    background: $t1_hint;
    color: white;
    border-radius: $dim;
    padding:$dim;
    width: 600px;
    left: 50%;
    margin-left: -300px;
    top: $dim;
    margin-top: 0px;
    z-index: 10000;
}

.defcon-blocked {
    width: 100%;
    height:100%;
    z-index: 9999;
    position:absolute;
    background: rgba($color: #000000, $alpha: .75)
}

@media print
{    
    .no-print, .no-print *
    {
        display: none !important;
    }
}

.teaserTop {
    width: 100%;
    background: #AAB338;
}

.teaserTop .inner {
    color: #fff;
    font-weight: bold;
    cursor: pointer;
}