$themes: (
  light: (
    backgroundColor: $t1_main_background,
    color: $t1_main_color,
    sidePanelBackground: $t1_sidepannel_bg,
    userControlBackground: $t1_uc_bg,
    availabilityColor: $t2_text_main,
    textMain: $t1_text_main,
    textDesc: $t1_text_desc,
    buttonDisabledBackground: $t1_button_disabled,
    buttonDisabledColor: $t1_button_disabled_color,
    teaserBackground: $t2_main_background
  ),
  dark: (
    backgroundColor: $t2_main_background,
    color: $t2_main_color,
    sidePanelBackground: $t2_sidepannel_bg,
    userControlBackground: $t2_uc_bg,
    availabilityColor: $t1_text_main,
    textMain: $t2_text_main,
    textDesc: $t2_text_desc,
    buttonDisabledBackground: $t2_button_disabled,
    buttonDisabledColor: $t2_button_disabled_color,
    teaserBackground: $t1_main_background   
  )
);

@mixin themify($themes: $themes) {
  @each $theme, $map in $themes {
    [data-theme="theme-#{$theme}"] & {
      $theme-map: () !global;
      @each $key, $submap in $map {
        $value: map-get(map-get($themes, $theme), "#{$key}");
        $theme-map: map-merge(
          $theme-map,
          (
            $key: $value
          )
        ) !global;
      }

      @content;
      $theme-map: null !global;
    }
  }
}

@function themed($key) {
  @return map-get($theme-map, $key);
}
