@import "_main.scss";

.sidepannel {
  @include themify($themes) {
    background-color: themed("sidePanelBackground");
  }
  width: 100px;
  position: fixed;
  right: 0px;
  top: 0px;
  height: 100%;
  z-index: 999;
}

.sidepannel .inner {
  padding: 0px;
}

.sidepannel.open {
  width: 320px;
}

.zero {
  width:0px;
  height:0px;
}

.user {
  width: 48px;
  height: 48px;
  background: #333;
  border: 1px solid #999;
  border-radius: 24px;
  background-size: cover;
}

.lifetime {
  position: absolute;
    right: 2px;
    top: 12px;
    font-size: 10px;
}

.lifetime span.min {
  font-size: 16px;
  font-weight: bold;
}

.user-container.open .user {
  margin-left: 100px;
}

.user-container {
  position: fixed;
  right: 0px;
  top: $dimx2;
  width: 76px;
  z-index: 1000;
}

.user-container .inside {
  border-radius: 24px 0px 0px 24px;
  @include themify($themes) {
    background: themed("userControlBackground");
  }
  height: 52px;
  padding: 2px;
  position: relative !important;
}

.user-container .action {
  width: 20px;
  float: left;
  height: 30px;
  position: absolute;
}

.user-container.open .action {
  display: none;
}

.user-container.open .inside {
  height: 230px;
}

.user-container.open {
  width: 270px;
}

.user-container .close .icon {
  display: none;
  margin-top: 5px;
}

.sidepannel .tool-container {
  margin-top: 20px;
  position: relative;
}

.user-container .close .icon,
.tool-container .action .icon {
  margin-top: 22px;
  filter: $filter_action;
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.tool-container .action .icon {
  margin-top: 10px;
}

.navi-container {
  width:95%;
}

.navi-container,
.tool-container .tool {
  margin-left: 30px;
}

.tool-container .tool .info {
  position: relative;
}

.tool-container .tool .info .icon {
  width: 22px;
  height: 22px;
  background-size: cover;
  position: absolute;
  top: -2px;
}

.tool-container .tool .info .title {
  font-style: normal;
  font-weight: 900;
  font-size: 12px;
  line-height: normal;
  letter-spacing: 0.05em;
  margin: 10px 0px 10px 30px;
}

.tool-container .oe-control {
  float: left;
}

.tool .oe-control label span {
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 15px;
}

.tool .oe-radio label {
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  transition: all 0.5s ease;
  cursor: pointer;
  position: absolute;
  top: 6px;
  left: 6px;
  z-index: 1;
  background: transparent;
}

.tool .oe-checkbox label span,
.tool .oe-radio label span {
  position: absolute;
  left: 14px;
  top: -5px;
  font-style: normal;
  font-weight: 800;
  font-size: 10px;
  line-height: normal;
  letter-spacing: 0.05em;
}

.tool .oe-radio input[type="radio"]:checked ~ label {
  background: transparent;
}

.tool .oe-radio input[type="radio"]:checked ~ div.oe-border {
  background: $t1_action;
}

.tool .oe-radio input[type="radio"]:checked ~ label span {
  color: $t1_action;
}

.tool .oe-radio .oe-border {
  width: 16px;
  height: 16px;
  background: transparent;
  border: 1px solid #fff;
  border-radius: 8px;
  position: absolute;
}

.tool.open {
  display:block !important;
  position: absolute;
  top: 80px;
  left:$dim;
}

.tool.open button {
  padding:10px;
}

.tool .input-round {
  border: 1px solid #fff;
}

.tool-name {
  position: relative;
}

.tool-name .personal {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: normal;
  color: $t1_text_main;
}

.tool-name .company {
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 15px;
  color: $t1_text_desc;
}

.tool-container .action {
  position: absolute;
}

.tool-container > .tool {
  margin-top: 20px;
  width: 260px;
}

.tool-container > .tool:first-child {
  margin-top: 0px;
}

.sidepannel .tool {
  display: none;
}

.sidepannel.open .tool {
  display: block;
}

.sidepannel.open .action.open .icon {
  transform: rotate(180deg);
}

.user-container.open .close .icon {
  margin-right: 5px;
  display: block;
}

.user-container .close .icon:hover .tool-container .action .icon:hover {
  filter: $filter_ready;
}

.user-container .tools {
  display: none;
}

.user-container .tools .licon {
  margin-bottom: 10px;
}

.user-container .inside .sep {
  height: 1px;
  width: 100%;
  margin: 20px 0px 20px 20px;
  background: $t1_sidepannel_sep;
}

.user-container.open .tools {
  display: block;
}

.sidepannel .navi-container {
  display: none;
}

.sidepannel.open .navi-container {
  position: absolute;
  display: block;
  bottom: 50px;
}

.sidepannel .navi-container .sep {
  height: 1px;
  width: 100%;
  background: $t1_sidepannel_sep_light;
  margin: 20px 0px;
}

.sidepannel .navi-container button.licon {
  margin: 10px 0px;
}

.infobox .sep {
    width:100%;
    height:1px;
    margin: $dimx3 0;
    background: $t1_sidepannel_sep;
}