@import "_main.scss";

.bicon {
  text-align: center;
}
.iprogress .bicon {
  width: $width_thumb;
  float: left;
  margin-left: 5px;
  height: 65px;
}
.iprogress {
  text-align: center;
  position: relative;
}
.iprogress .iprogress-bg {
  z-index: 1;
  background: #fff;
  border-radius: 8px;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 96px;
  width: 150px;
}
.iprogress .inner {
  margin: 0 auto;
  z-index: 900;
  width: 440px;
  position: relative;
}
.iprogress .iprogress-icons {
  z-index: 2;
  position: relative;
  margin-top: 10px;
}
.iprogress.block {
  margin-top: 0px;
}

#app.fix .iprogress-icons .bicon {
  height: $dimx4;
}

#app.fix .iprogress-icons .bicon .icon {
  width: $dimx4;
  height: $dimx4;
  background-size: cover;
  float: left;
}

#app.fix .iprogress-icons .bicon .text {
  float: left;
  margin-left: $dim;
  margin-top: $dim;
}

.iprogress .iprogress-bg.tariff {
  width: 150px;
}

.iprogress .iprogress-bg.overview,
.iprogress .iprogress-bg.customer {
  width: 300px;
}

.iprogress .iprogress-bg.summary,
.iprogress .iprogress-bg.done {
  width: 450px;
}


.iprogress .iprogress-bg.deliveryAddress {
  width: 300px;
}

.iprogress .iprogress-bg.orderOverview {
  width: 450px;
}

.bicon .icon {
  margin: auto;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
}
.bicon .icon.tariff {
  background-image: url(../assets/icons/big_tarif.svg);
}
.bicon .icon.deal {
  background-image: url(../assets/icons/big_deal.svg);
}
.bicon .icon.customer {
  background-image: url(../assets/icons/big_user.svg);
}
.bicon .icon.contract {
  background-image: url(../assets/icons/big_contract.svg);
}
.bicon .icon.summary {
  background-image: url(../assets/icons/big_summary.svg);
}

.bicon.disabled {
  filter: $filter_disabled;
}
.bicon.ready {
  filter: $filter_ready;
}
.bicon.selected {
  filter: $filter_selected;
}

button.link,
button.licon {
  background: none !important;
  color: inherit;
  border: none;
  padding: 0 !important;
  font: inherit;
  /*border is optional*/
  border-bottom: 0px;
  cursor: pointer;
  position: relative;
  filter: $filter_disabled;
  width: auto;
}

.licon.action3,
.container-action button.licon {
  margin-top: $dim;
}

.container-action button.licon.action .text,
.container-action button.licon.action1 .text {
  filter: $filter_action;
}

.container-action button.licon.action2 .text {
  filter: $filter_action2;
}

button.licon.action2 {
  filter: $filter_action2;
}

button.licon.action4 .text {
  filter: $filter_action4;
}

button.link:hover,
button.licon:hover {
  filter: $filter_ready;
}

button.link .text,
button.licon .text {
  text-align: left;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  margin-left: 28px;
  margin-top: 4px;
}


button.link .text {
  margin-left: 0px;
}

button.right.licon .text {
  clear: right;
  display: inline;
  margin-right: $dim;
  margin-left:0px;
}

button.aicon {
  background: $t1_action;
  border-radius: 56px;
  color: $t1_action_color;
  width: auto;
  position: relative;
}

button.aicon .text {
  padding: 0px 20px;
}

button.aicon .icon {
  width: 24px;
  height: 24px;
  filter: $filter_button_color;
}
button.aicon:hover {
  background: $t1_input_base;
}

.licon .icon {
  width: 22px;
  height: 22px;
  position: absolute;
  left: 0px;
  top: 0px;
}

button.right.licon .icon {
  position: initial;
  float:right;
}

.aicon .icon {
  margin: auto;
  width: 40px;
  height: 50px;
  background-repeat: no-repeat;
  position: absolute;
  right: $dim;
  top: $dim;
}

button.aicon.disabled {
  @include themify($themes) {
    background: themed("buttonDisabledBackground");
    color: themed("buttonDisabledColor");
    cursor: none;
  }
}

.icon.chevron {
  background-image: url(../assets/icons/chevron.svg);
}

.contactsGrid-contact-icon {
  background-image: url("../assets/images/me.jpg");
  width: 44px;
  height: 44px;
  border: 1px solid #999;
  border-radius: 24px;
  background-size: cover;
}

.brandi {
  width: 60px;
  height: 60px;
  float: left;
  background-repeat: no-repeat;
  background-position: 0px 0px;
  position: relative;
}
.brandi.vf {
  background-image: url(../assets/images/brand/thumb-vf.svg);
}
.brandi.otelo {
  background-image: url(../assets/images/brand/thumb-otelo.svg);
}
.brandi.dsl {
  background-image: url(../assets/images/brand/thumb-dsl.svg);
}
.brandi.unity,
.brandi.kabel {
  background-image: url(../assets/images/brand/thumb-kabel.svg);
}
.brandi.tv {
  background-image: url(../assets/images/brand/thumb-tv.svg);
}

.brandi.fn {
  background-image: url(../assets/images/brand/thumb-fn.svg);
}
.brandi.dsl2  {
  background-image: url(../assets/images/brand/dslIcon.svg);
}
.brandi.gigaKombi{
  background-image: url(../assets/images/brand/gigakombibanner.svg);
  width: 100% !important;
  float: none !important;
}

.brandi.selector {
  opacity: 0.25;
  width: 50px;
  height:50px;
  cursor: pointer;
}

.brandi.selector.selected {
  opacity: 1;
}

.brandi .desc {
  font-size: 8px;
  color: white;
  width:46px;
  margin-top: 32px;
  text-align: center;
  bottom: 4px;
}

.brandi.dsl .desc {
  color: $t1_brand4_text;
}

.bicon .text {
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 15px;
  text-align: center;
  color: #828282;
}



.iicon {
  width: 22px;
  height: 22px;
}

.icon32 {
  width: 32px;
  height: 32px;
}

.iicon:hover {
  filter: $filter_action;
}

.licon.action3 .text, 
.block-contract button.licon.action3 .text,
.container-action button.licon .text {
  font-style: normal;
  font-weight: 800;
  font-size: 14px !important;
  line-height: normal;
  letter-spacing: 0.05em;
