@import '_main.scss'; 

.popup {
    width:560px;
    height: 260px;
    border: 1px solid;
    background: $t1_popup_bg;
    border-radius: $dimx2;
    position: fixed;
    border: 1px solid #cecece;
    // top: calc(40% - 100px);
    // left: calc(50% - 200px);
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
    z-index: 5000;    
    -webkit-box-shadow: -5px 10px 24px -6px rgba(0,0,0,0.75);
    -moz-box-shadow: -5px 10px 24px -6px rgba(0,0,0,0.75);
    box-shadow: -5px 10px 24px -6px rgba(0,0,0,0.75);
}

.popup.wide {
    width:700px;
    top: 50%;
    left: 50%;    
    //top: calc(30% - 100px);
    //left: calc(50% - 350px);    
}

#app.edge .popup.wide{
    top: 50%;
    left: 50%;   
}

.popup-overlay {
    position:fixed;
    top:0px;
    left:0px;
    width:100%;
    height:100%;
    background: rgba(0,0,0,0.3);
    z-index: 4999;
}

.popup .inner {
    position:relative;
    padding: $dimx2;
    height:100%;
    width:100%;
    text-align:center;
}

.popup .inner .sep {
    width:100%;
    height:1px;
    margin: $dim 0;
    background: $t1_sidepannel_sep;
}

.popup .inner .title {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: normal;
    color:$t1_text_main;
    background: transparent;
    text-align:center;
    margin-bottom: $dimx2;
}

.popup .action-container {
    position: absolute;
    bottom: $dim;
    width: calc(100% - 32px);
}

.popup .action-container button.licon {
    margin-top: $dim;
}

.popup .content {
    height: calc(100% - 100px);
    overflow:auto;
}

.popup.noact .content {
    height: calc(100% - 40px);
}

.popup .close {
    position: absolute;
    width: $dimx3;
    height: $dimx3;
    right: $dim;
    cursor: pointer;
    background: url('../assets/icons/close.svg');
}

.popup .close:hover {
    filter: $filter_action;
}

.tloader {
    text-align: center;
}

.tloader .bg {
    background: url('../assets/images/loaderr.gif');
    width:80px;
    height:80px;
    background-size: cover;
    margin:0 auto;
}

.tloader .text {
    font-style: normal;
    font-weight: 600;
    font-size: 11px;
    line-height: 15px;
    margin-top: $dimx2;
    color: $t1_text_desc;
}

.contentTooltip .__react_component_tooltip {
    max-width: fit-content !important;
}

.hint img.icon {
    width: 14px;
    height: 14px;
    filter: $filter_action4;
}