@import "_main.scss";

@media print {
    #container-main {
        padding-top:10px !important;
    }

    .block-act-contract .thumb-brand-blister {
        -webkit-print-color-adjust: exact;
    }

    .block-act-contract .thumb-brand-blister .thumb-inner {
        -webkit-print-color-adjust: exact;
    }

    .block-act-preview .container {
        margin-left: 160px !important;
        padding-right:160px !important;
    }

    .info {
        display: block;
    }

    .page-break-avoid {
        page-break-inside: avoid !important;
    }

    .print-hide {
        display:none;
    }

    .block-contract {
        margin-bottom: 0px;
    }
}
