@import "_main.scss";


html,
body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  @include themify($themes) {
    background-color: themed("backgroundColor");
    color: themed("color");
  }
  
  font-family: "Muli", sans-serif;
}

.red {
  color: #e50800 !important;
}
.lila {
  color: #8f359b !important;
}
.white {
  color: #fff !important;
}
.black {
  color: #000 !important;
} 
.green {
  color: #090 !important;
}
.orange {
  color: #f90 !important;
}

.link {
  cursor: pointer;
}

.uc {
  text-transform: uppercase;
}

.lc {
  text-transform: lowercase;
}

.dn {
  display: none !important;
}

.tal {
  text-align:left;
}

.tar {
  text-align: right;
}

.tac {
  text-align: center;
}



.noov {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;  
}

.posf {
  position: fixed;
}

.center {
  text-align: center;
}
.np {
  padding: 0px !important;
}

.bl {
  display: block !important;
}
.wa {
  width:auto !important;
}
.mwa {
  min-width:auto !important;
}

.footer {
  height: 40px;
  position: fixed;
  bottom: 0px;
  width: 100%;
  background: #828282;
  z-index: 1000;
  text-align: center;
  font-size: 14px;
}

.header {
  position: fixed;
  z-index: 998;
  width: 100%;
  height: $dimx11;
}

.header.fix {
  background-color: rgba(226, 226, 226, 0.95);
}

.header.hassub {
  height: 142px;
}

#app.fix .header {
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.25);
}

#app.fix .header .sub {
  padding-top: 56px;
}

.header .sub {
  padding-top: 72px;
}

#app.fix .iprogress-bg {
  display: none;
}

.header .wrapper {
  width: 100% !important;
  position: relative;
  text-align: center;
}

.header .wrapper .logo {
  margin-left: 0px;
  padding-top: 19px;
  position: absolute;
  z-index: 500;
}

.header .wrapper .title {
  margin: 0 auto;
  position: fixed;
  text-align: center;
  width: calc(100% - 0px);
  margin-top: $dimx3;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: normal;
}

#app.exp .header .wrapper .title {
  width: calc(100% - 320px);
}

#app.exp .block.iprogress {
  width: calc(100% - 320px);
}

.footer .wrapper .logo {
  position: absolute;
  right: 5px;
  top: 10px;
}

.sidepannel {
  background: #333;
  width: 50px;
  position: fixed;
  right: 0px;
  top: 0px;
  height: 100%;
  z-index: 999;
}

.sidepannel.expanded {
  width: 300px;
}

.sidepannel .user {
  width: 30px;
  height: 30px;
  background: #333;
  border: 1px solid #999;
  border-radius: 15px;
  margin: 5px;
  background-size: cover;
}

.inner {
  padding: 5px;
}

.fl {
  float: left;
}
.fr {
  float: right;
}

.infobox {
  background: #fff;
  border-radius: $dimx2;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: normal;
  padding: $dimx2;
  color: #828282;
  margin-top:66px;
  width: 314px;
}

.infobox.fixed {
  position: fixed;
  top: 260px;
  margin-top:0px;
}

#app.fix .infobox {
  top: 160px;
}

.infobox .inner {
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  border-radius: 8px;
}

.infobox .inner .product {
  margin-left: 70px;
  margin-top: 20px;
}

.infobox .inner .product .title {
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: normal;
  letter-spacing: 0.05em;
  color: #333333;
  width: 70%;
}

.infobox .info label {
  font-size: 10px;
}

.infobox .info label.best {
  font-size: 11px;
  font-weight: bold;
  color: #333;
}

.infobox .infor {
  text-align: right;
}

.infobox .info label.big {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: normal;
  color: #333333;
}

.infobox .action {
  position: absolute;
  right: 25px;
}

.infobox .action .iicon {
  float: left;
}

.statusbox {
  width: $dimx11;
  height: $dimx11;
  border-radius: 44px;
  border: 3px solid;
  margin: auto;
}

.statusbox.st1 {
  filter: $filter_disabled;
}
.statusbox.st2 {
  filter: $filter_ready;
}
.statusbox.st4 {
  filter: $filter_failed;
}
.statusbox.st16 {
  filter: $filter_warning;
}

.statusbox .icon {
  width: $dimx6;
  height: $dimx6;
  margin-left: 18px;
  margin-top: 18px;
}
.popup .statusbox .icon {
  width: $dimx6;
  height: $dimx6;
  margin-left: 0px;
  margin-top: 18px;
}

.statusbox.st1 .icon {
  background: url("../assets/icons/loading.svg");
}
.statusbox.st2 .icon {
  background: url("../assets/icons/ok.svg");
}
.statusbox.st4 .icon {
  background: url("../assets/icons/error.svg");
}
.statusbox.st16 .icon {
  background: url("../assets/icons/exclaim.svg") no-repeat;
  background-size: 48px 48px;
}

.statusbox.st1 .icon {
  animation-name: stbr;
  animation-duration: 2s;
  /* Things added */
  animation-iteration-count: infinite;
  transform-origin: 50% 50%;
  display: inline-block;
}

@keyframes stbr {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.wrapper {
  position: relative;
  width: 900px;
  margin: 0 auto;
}

.inner2 {
  position: relative;
}

.loader {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100px;
  height: 100px;
  border-radius: 3px;
}

.clear {
  clear: both;
}

.bg-circle {
  width: 500px;
  height: 500px;
  border-radius: 250px;
  position: absolute;
  top: 5%;
  left: 5%;
  z-index: 0;
  background: #333;
  opacity: 0.05;
}

#root {
  z-index: 2;
}

#appback {
  background-image: url(https://sso.otelo-easy.de/sso/content/img/bg1.jpg);
  background-size: cover;
  opacity: 0.55;
  position: fixed;
  left:0px;
  top:0px;
  width:100%;
  height:100%;
}


.error,
.error .details {
  color: $t1_error;
}

.warn .details {
  color: $t1_sidepannel_action2;
}

.success .details {
  color: $t1_success;
}

