/*block*/
.block {
  margin-top: $dimx2;
  clear: both;
  background: #fff;
  box-shadow: 0px 24px 24px rgba(33, 33, 33, 0.1) !important;
  border-radius: $dimx2;
  padding: $dimx2 !important;
}

.block.graygradient {
  color: #F2F2F2;
  background: linear-gradient(65.92deg, #828282 0%, #333333 100%) !important;
}

.block.withicon {
