}

.block .block-icon {
  position:absolute;
  top: -20px;
}

.block.block-trans {
  background: none !important;
  box-shadow: none !important;
  padding: 0px !important;
}

.block .title {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: normal;
  margin-bottom: 20px;
