@import "_main.scss";

.thumb {
  width: 136px;
  float: left;
  margin-right: 10px;
  cursor: pointer;
  font-size: 14px;
}

.block-thumbs {
  margin: 0 $dimx3;
}

.thumb.thumb-brand-blister {
  height: 238px;
  position: relative;
  cursor: pointer;
}

.thumb.thumb-brand-blister .thumb-inner {
  height: 196px;
  margin-top: 15px;
  border-radius: 8px;
  transition: 0.2s ease all;
  background-repeat: no-repeat;
}

.thumb.thumb-brand-blister .thumb-inner:hover {
  margin-top: 0px;
  -webkit-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
}

.thumb.thumb-brand-blister.bad .thumb-inner {
  opacity: 0.5;  
  border:1px solid;
}

.thumb.thumb-brand-blister.bad .thumb-inner {
  margin-top: 15px;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

.thumb.thumb-brand-blister .thumb-inner.selected {
  margin-top: 0px;
  -webkit-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
}

.thumb.thumb-brand-blister .thumb-inner.selected.flat,
.thumb.thumb-brand-blister .thumb-inner.flat {
    margin-top: 15px !important;
    -webkit-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.25);
    box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.25);
}

.thumb.thumb-brand-blister .thumb-inner.flat:hover {
  margin-top: 0px;
  -webkit-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.75);
}

.thumb.thumb-brand-blister .info {
  position: absolute;
  bottom: 0px;
  width: 100%;
  border-radius: $dim;
  font-size: 10px;
  text-align: center;
  padding: 2px;
}

.thumb.thumb-brand-blister .info span {
  width: 100%;
  text-align: center;
  @include themify($themes) {
    color: themed("availabilityColor");
  }
}

.thumb.thumb-brand-blister .info.bad {
  background: $t1_text_main;
  @include themify($themes) {
    color: themed("userControlBackground");
  }
}

.thumb.thumb-brand-blister .info.main {
  background: $t1_input_base;
}

.thumb.thumb-brand-blister .info.high {
  background: $t1_sidepannel_action2;
}

.thumb.thumb-brand-blister .markok {
  position: absolute;
  width: $dimx3;
  height: $dimx3;
  bottom: $dimx7;
  right: $dim;
  background: url("../assets/images/checkok.png");
}

/*in block*/
.block-container.selonly .thumb-inner {
  opacity: 0.25;
  margin-top: 15px;
  cursor: auto;
}

.block-container.selonly .thumb-inner.selected {
  opacity: 1;
}
.block-container.selonly .thumb-inner:hover {
  margin-top: 15px;
}

.block-inner .block-action ,
.block-container .block-action {
  position: absolute;
  right: 16px;
  top: 24px;
}

.thumb.thumb-brand-blister .vf {
  background-image: url(../assets/images/brand/blister-vf.svg);
}

.thumb.thumb-brand-blister .otelo {
  background-image: url(../assets/images/brand/blister-otelo.svg);
}

.thumb.thumb-brand-blister .dsl {
  background-image: url(../assets/images/brand/blister-dsl.svg);
}


.thumb.thumb-brand-blister .kabel {
  background-image: url(../assets/images/brand/blister-kabel.svg);
}

.thumb.thumb-brand-blister .fn {
  background-image: url(../assets/images/brand/blister-fn.svg);
}

.thumb.thumb-brand-blister .tv {
  background-image: url(../assets/images/brand/blister-tv.svg);
}

.thumb.thumb-brand-blister .tv_fn {
  background-image: url(../assets/images/brand/blister-tv-fn.png);
}

.thumb.thumb-brand-blister .dsl.loading {
  background-image: url(../assets/images/brand/blister-dsl-loading.svg);
}

.thumb.thumb-brand-blister .kabel.loading {
  background-image: url(../assets/images/brand/blister-kabel-loading.svg);
}

.thumb.thumb-brand-blister .tv.loading {
  background-image: url(../assets/images/brand/blister-tv-loading.svg);
}

.thumb.thumb-brand-blister .fn.loading {
  background-image: url(../assets/images/brand/blister-fn-loading.svg);
}

.thumb.thumb-brand-square {
  height: 90px;
  cursor: pointer;
}

.thumb.thumb-brand-square .thumb-inner {
  height: 90px;
  background-position: -25px 0px;
}

.thumb.thumb-brand-square .vf {
  background-image: url(../assets/images/brand/square-vf.svg);
}

.thumb.thumb-brand-square .otelo {
  background-image: url(../assets/images/brand/square-otelo.svg);
}

.thumb.thumb-brand-square .dsl {
  background-image: url(../assets/images/brand/square-dsl.svg);
}

.thumb.thumb-brand-square .kabel {
  background-image: url(../assets/images/brand/square-kabel.svg);
}

.thumb.thumb-brand-square .tv {
  background-image: url(../assets/images/brand/square-tv.svg);
}

.thumb.thumb-brand-square .fn {
  background-image: url(../assets/images/brand/square-fn.svg);
}

.thumb.thumb-tariff {
  width: 136px;
  height: 76px;
  border-radius: 8px;
  text-align: center;
  margin-right: 10px;
  margin-bottom: 8px;
  background: #f2f2f2;
  /* Internet Explorer 10 */
  display: -ms-flexbox;
  -ms-flex-pack: center;
  -ms-flex-align: center;
  /* Firefox */
  display: -moz-box;
  -moz-box-pack: center;
  -moz-box-align: center;
  /* Safari, Opera, and Chrome */
  display: -webkit-box;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  /* W3C */
  /*display: flex;*//*removed for center*/
  box-pack: center;
  box-align: center;
  color: #000;
}

.thumb.thumb-w2 {
  width: 282px;
}

.thumb.thumb-small {
  width:40px;
  height:20px;
}

.thumb.thumb-tariff.sub {
  height: 36px;
}

.thumb.thumb-tariff.sub.unity,
.thumb.thumb-tariff.sub.kabel {
  height: 76px;
}

.thumb.thumb-tariff.opt.kabel {
  height: 76px;
  width: 186px;
  background: #f2f2f2;
  color: #333;
}

.thumb-tariff.selected {
  background: #333 !important;
  color: #fff !important;
}

.thumb-tariff.removable {
  background: $t1_action !important;
}

.thumb-tariff.autoremove {
  background: $t1_input_hint !important;
}

.thumb-tariff.notremovable {
  background: $t1_hint !important;
}

.thumb-tariff.insel {
  border:2px solid $t1_action;
}

.thumb.thumb-tariff.vf {
  color: $t1_brand1_text;
  background: $t1_brand1_main;
  height: 72px;
}

.thumb.thumb-tariff.otelo {
  color: $t1_brand2_text;
  background: $t1_brand2_main;
}

.thumb.thumb-tariff.kabel,
.thumb.thumb-tariff.dsl {
  color: #000;
  background: #fff;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.25);
}

.thumb.thumb-tariff.sub.dsl {
  height: 72px;
}

.thumb.thumb-tariff.sub {
  background: #f2f2f2;
  color: #333;
}

.thumb.thumb-tariff.sub.selected.dsl {
  background: #333;
  color: #fff;
}

.thumb.thumb-blister {
  width: 136px;
  height: 186px;
  border-radius: 8px;
  position: relative;
}

.thumb.thumb-blister .title {
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 18px;
  align-items: center;
  text-align: center;
  letter-spacing: 0.05em;
  color: $t1_brand1_text;

  position:absolute;
  width:136px;
  margin-top: $dimx2;
}

.thumb.thumb-blister .teaser {
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 15px;
  display: flex;
  align-items: center;
  text-align: center;
  color: $t1_main_color;
  background: $t1_brand1_text;
  margin: 4px;

  position:absolute;
  border-radius: 4px;
  width:128px;
  margin-top: $dimx9;
}

.thumb.thumb-blister .teaser .inner {
  width:100%;
}

.thumb.thumb-blister .subtitle {
  font-weight: 400;
  font-size: 10px;
  line-height: 15px;
  width:136px;
  font-style: normal;
  align-items: center;
  text-align: center;
  color: $t1_brand1_text_sub;

  position:absolute;
  margin-top: $dimx5;
}

.thumb.thumb-blister.vf {
  background: $t1_brand1_main;
}

.thumb.thumb-blister.otelo {
  background: $t1_brand2_main;
}

.thumb.thumb-blister.kabel {
  background: $t1_brand3_main;
}

.thumb.thumb-blister.dsl {
  background: $t1_brand4_main;
}

.product-item .price,
.thumb.thumb-blister .price {
  position:absolute;
  text-align: center;
  width:130px;
  margin-top: $dimx14;
}

.product-item .price .pricewrapper,
.thumb.thumb-blister .price .pricewrapper {
  position:relative;
}

.product-item .price .price-unit,
.thumb.thumb-blister .price .price-unit {
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
  text-align: right;
  color:$t1_brand1_text;
}

.thumb.thumb-blister .price .price-subunit {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 15px;
  color:$t1_brand1_text;
}

.product-item .price table,
.thumb.thumb-blister .price table {
  width: auto;
  margin: auto;
}

.product-item .price .price-desc,
.thumb.thumb-blister .price .price-desc {
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 25px;
  color:$t1_brand1_text;
}

.thumb.thumb-blister.dsl .teaser,
.thumb.thumb-blister.kabel .teaser {
  background:$t1_brand3_text;
  color:$t1_brand3_main;
}

.thumb.thumb-blister.dsl .title ,
.thumb.thumb-blister.dsl .price .price-unit ,
.thumb.thumb-blister.dsl .price .price-subunit ,
.thumb.thumb-blister.dsl .price .price-desc,
.thumb.thumb-blister.kabel .title ,
.thumb.thumb-blister.kabel .price .price-unit ,
.thumb.thumb-blister.kabel .price .price-subunit ,
.thumb.thumb-blister.kabel .price .price-desc {
  color:$t1_brand3_text;
}

.thumb.thumb-blister .icon {
  position:absolute;
  top: 170px;
  left: 65px;
  background-size:18px;
  background-repeat: no-repeat;
}

.thumb.thumb-blister.vf .price .pricewrapper div:nth-child(1),
.thumb.thumb-blister.vf .price .pricewrapper div:nth-child(2),
.thumb.thumb-blister.vf .price .pricewrapper div:nth-child(3) {
  color: $t1_brand1_text;
}

.blisterAditionalText{
  font-size: 10px;
}


.thumb-sim {
  width: 289px;
  height: 179px;
  border: 1px solid #E0E0E0;
  box-sizing: border-box;
  border-radius: $dimx2;
  position: relative;
  margin-right: $dimx2;
  float:left;
}

.thumb-sim .title {
  margin-left: $dimx2;
  margin-top: $dimx2;
}

.thumb-sim .content {
  margin-left: $dimx2;
  margin-top: $dimx2;
}

.thumb-sim .action {
  position: absolute;
  right: $dim;
  bottom: $dim;
}

.thumb-service {
  width: 188px !important;
  height:114px !important;
  position: relative;
  border: 1px solid $t1_action;
}

.thumb-service span.text {
  font-style: normal;
  // font-weight: 600;
  font-size: 20px;
  line-height: 25px;
  position: absolute;
  left: $dimx2;
  top: $dimx2;
}

.thumb-service .action {
  position: absolute;
  bottom: $dim;
  right: $dim;
  text-transform: uppercase;
  color: $t1_action;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 18px;
}

.thumb-service .action:hover {
  color: $t1_input_base;
}

.thumb-service.sel .action{
  color: $t1_action_color;
}