@import '_main.scss'; 

.oe-table {
    background: transparent;
    box-shadow: 0px 24px 24px rgba(51, 51, 51, 0.1);
}

.oe-row.odd .data{
    background: $t1_row_odd;
}

.oe-row.even .data{
    background: $t1_row_even;
}

.oe-table .col {
    float: left;
    padding:8px;
}

.container.wide {
    max-width:98% !important;
}

.oe-row .head:first-child {
    border-top-left-radius: $dimx2;
}

.oe-row .head:nth-last-child(2) {
    border-top-right-radius: $dimx2;
}

.oe-row .col.data {
    height:60px;
    // white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
    display: flex;
}

.oe-row .col.data .iicon,
.oe-row .col .brandi {
    margin-top: $dimx2;
    margin-right: 4px;
}

.oe-table .last {
    border-bottom-left-radius: $dim;
    border-bottom-right-radius: $dimx2;

    background: $t1_table_head;
    color: $t1_row_even;
    height: $dimx7;
    padding: $dim;
}

.oe-table .head  {
    background: $t1_table_head;
    color: $t1_row_even;
    height: $dimx7;
    padding: $dimx2;
    display: flex;
    position: relative;

    .col-head-text {
        margin-left: $dim;
    }
    .col-head-sort-icons {
        position: absolute;
        left: 4px;
        top: 18px;
    
        .icon {
            width: 16px;
            height: 16px;
        }

        .icon:hover{
            color: #ccc;
            cursor: pointer;
        }

        .selected {
            color: red;
        }
    }
}

.oe-table .oe-row:hover .data{
    background: #ccc;
    cursor: pointer;
}


.react-datepicker-popper {
    z-index: 1000;
}