label {
    text-align:center;
     font-size: 1rem;
     margin-bottom: 0px;
 }

 .lowerLabel{
    font-family: "Muli", sans-serif;
    font-size: 0.8rem;
    font-weight: 400;
    line-height: 1.5;
    margin-left: 45%;
    
 }
 
 progress {
     width: 100%;
     height: 14px;
     margin: 5px 0 0;
     border-radius: 2px;
     display: block;
     /* Important Thing */
     -webkit-appearance: none;
     border: none;
 }
 
 /*  style the background */
 progress::-webkit-progress-bar {
     background: #E0E0E0;
     border-radius: 2px;
     padding: 0px;
    
 }
 

 progress::-webkit-progress-value {
     
     background:
         -webkit-linear-gradient(left, #4EAEC7,  #4EAEC7);
 }